.container{
  max-width: 400px;
  min-height: 450px;
  margin: 0 auto;
  background-color: teal;
  box-shadow: 0 0 10px gray;
  margin-top: 50px;
  padding: 15px 50px;
}

.text-center {
  text-align: center;
}

.expense-container {
  display: flex;
  justify-content: space-around;
  background-color: cadetblue;
  box-shadow: 0 0 10px gray;
  border-radius: 8px !important;
}

.transaction-list {
  margin: 0;
  padding: 0;
}

.transaction-list > li {
  display: flex;
  justify-content: space-between;
  box-shadow: 0 0 10px gray;
  padding: 10px 15px;
  background-color: white;
  margin: 5px 0;
  border-radius: 4px;
}

.transaction-form input {
  width: 100%;
  margin: 10px 0;
  padding: 5px 5px;
}

.btn{
  width: 50% !important;
  border-radius: 6px;
  border: none;
  margin-left: 100px !important;
  font-family: cursive;
}
.btn:hover{
  background-color: lightblue;
}